import React, { useEffect, useState, Fragment } from 'react'
import Banner from './Banner'
import Listingwrapper from './Listingwrapper'
import { useLocation } from 'react-router-dom'
import { connect } from 'react-redux'
import { load_single_property } from '../../../redux/actions/listings'

const Content = ({ load_single_property, listing, home_page }) => {

  const [singleProperty, setSingleProperty] = useState({})
  const [propertyImages, setPropertyImages] = useState([])
  const [propertyThumbs, setPropertyThumbs] = useState([])
  const location = useLocation()

  useEffect(() => {
    const id =
      location.pathname.split('/')[location.pathname.split('/').length - 1]
    load_single_property(id)
  }, [location])

  useEffect(() => {
    if (listing) {
      setSingleProperty(listing)
      if (listing.property_gallary) {
        setPropertyImages(
          listing.property_gallary.map(item => ({
            img: item.image,
          }))
        )
        setPropertyThumbs(
          listing.property_gallary.map(item => ({
            img: item.tmb_image,
          }))
        )
      }
    }
  }, [listing])

  return (
    <Fragment>
      {singleProperty && (
        <>
          <Banner listing={singleProperty} />
          <Listingwrapper
            listing={singleProperty}
            mainslider={propertyImages}
            thumbslider={propertyThumbs}
            recent={home_page.recently_added}
          />
        </>
      )}
    </Fragment>
  )
}

const mapStateToProps = state => ({
  home_page: state.home.home,
  listing: state.listings.listing,
})

export default connect(mapStateToProps, { load_single_property })(Content)
